.box {
  text-decoration: none;
  color: #333;
  background-color: aliceblue;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-bottom: 25px;
  transition: all 0.25s ease-in-out;
}

.box:hover {
  background-color: #333;
  color: snow;
  cursor: pointer;
  transform: scale(0.9);
}

.H1 {
  font-size: 2rem;
  margin: 0.5rem 0;
  padding: 0;
}

.insideDiv {
  margin-left: 30px;
  flex-grow: 11;
}

.outsideDiv {
  margin-right: 30px;
}

.onlineStatusDot {
  width: 30px;
  background-color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
}

small {
  display: block;
  padding: 0 0 10px;
}
